.App{
    display: flex;
    flex-direction: column;
    background-color: antiquewhite;
    width: fit-content;
}

.about-me{
    display: flex;
    background-color: antiquewhite;
}

.about-me-text{
    padding: 5vw;
    height: 50vh;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}

.about-me-text-header{
    font-size: 4em;
    font-weight: 800;
}

.atlas-image{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 70vh;
}

.contact-image{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 30vh;
}

.atlas-image-pic{
    mix-blend-mode: multiply;
    transform: scale(1.5);
}

.work-experience{
    background-color: antiquewhite;
    display: flex;
}

.work-experience-image{
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.work-experience-timeline{
    padding: 5vw;
    display: flex;
    flex-direction: column;
}

.inst-image{
    transform: scale(0.3);
    mix-blend-mode: multiply;
}

.contact-image-pic{
    transform: scale(0.4);
    mix-blend-mode: multiply;
}

@media only screen and (max-width: 600px) {
    .about-me {
      flex-direction: column-reverse;
      width: 100vw;
    }

    .about-me-text{
        max-width: 450px;
        width: 100%;
    }

    .about-me-text-header{
        max-width: 400px;
    }

    .work-experience{
        flex-direction: column;
        width: auto;
    }

    .work-experience-timeline{
        max-width: 400px;
    }

    .atlas-image{
        width: 100vw;
    }

    /* .work-experience-image{
        width: 100%;
    } */

    .contact-image{
        width: 100%;
    }
  }